 promotional websites and trailers highlighting screenshots of Ledger as the Joker. Ledger\n                died on January 22, 2008, some months after the completed filming and six months before the film's\n                release from atoxic combination of prescription drugs, leading to intense attention from the\n                press and movie-going public.")]),_vm._v(" "),_c('p',[_vm._v("A co-production of the United States and the United Kingdom,The Dark Knightwas released\n                on July 16, 2008 in Australia, on July 18, 2008, in North America, and on July 25, 2008, in the\n                United Kingdom. Considered byfilm criticsto be one of the best films of the 2000s and\n                one of thebest superhero filmsever,[6][7]the film received highly positive\n                reviews, particularly for its screenplay, performances (particularly Ledger's), visual effects, and\n                direction, and set numerous records during its theatrical run.[8]The Dark Knightappeared\n                on more critics' top ten lists (287) than any other film of 2008 with the exception ofWALL-E,\n                and more critics (77) namedThe Dark Knightthe best film of 2008 than any other film\n                released that year.[9]With over $1billion in revenue worldwide, it became thehighest-grossing\n                film of 2008and is the33rd highest-grossing filmof all time, unadjusted for\n                inflation (4th at the time of release).[10]The film received eightAcademy Awardnominations;\n                it won the award forBest Sound Editingand Ledger was posthumously awardedBest\n                Supporting Actor.[11]The Dark Knight Rises, the final film in the trilogy, was released on\n                July 20, 2012.")]),_vm._v(" "),_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com/embed/EXeTwQWrcwY?rel=0&showinfo=0","frameborder":"0","allow":"autoplay; encrypted-media","allowfullscreen":""}})],1)])])}
